interface INavLink {
  id: number;
  name: string;
  route: string;
}

interface IRoutes {
  home: string;
  mainLayout: Record<string, string>;
}

interface IApi {
  authBaseUrl: string;
  moviesBaseUrl: string;
  authEndpoints: Record<string, string>;
}

export const API: IApi = {
  authBaseUrl: "https://norma.nomoreparties.space/api",
  moviesBaseUrl: "https://api.kinopoisk.dev/v1.4/movie",
  authEndpoints: {
    login: "/auth/login",
    register: "/auth/register",
    logout: "/auth/logout",
    forgotPassword: "/password-reset",
    resetPassword: "/password-reset/reset",
    userData: "/auth/user",
    refreshToken: "/auth/token",
  },
};

export const ROUTE: IRoutes = {
  home: "/",
  mainLayout: {
    movie: "movies",
    currentMovie: "movies/:movieId",
    favouritesMovies: "favourites",
    login: "login",
    register: "register",
    resetPass: "reset-password",
    forgotPass: "forgot-password",
    profile: "profile",
  },
};

export const navLinks: Array<INavLink> = [
  {
    id: 1,
    name: " ",
    route: `/${ROUTE.mainLayout.movie}`,
  },
  {
    id: 2,
    name: "",
    route: `/${ROUTE.mainLayout.profile}/${ROUTE.mainLayout.favouritesMovies}`,
  },
];
